<template>
  <div id="random" class="scrollTouch" :class="{hasHeader: $utils.showNavBar()}">
    <headerNav v-if="$utils.showNavBar()"></headerNav>
    <div class="carSwiper" ref="carSwiper">
      <div class="iconSize">
        <img :src="$utils.setImageSize(shareCarInfo.thumb, 400)" alt="">
      </div>
      <!-- <LoopMessage class="loopList" :list='loopList' v-if="loopList.length > 0"></LoopMessage> -->
      <!-- <div class="open_app flex-row" @click="toApp()" v-if="$config.source_style < 6">
        app打开
      </div> -->
    </div>
    <div class="carInfo">
      <!-- 顶部车辆信息 -->
      <div class="top">
        <div class="flex-row title">
          <div class="flex-row">
            <div class="fullName">{{$utils.splitStr(shareCarInfo.fullname, 20)}}</div>
         </div>
        </div>
        <div class="brand_logo iconSize">
          <img :src="shareCarInfo.brand_logo" alt="车标">
        </div>
        <div class="flex-row jusB topBody">
          <div class="flex-row leftPrice">
            <div class="price">
              <span class="redColor">0</span>
              元换座驾
            </div>
            <div class="acturally">&yen;{{shareCarInfo.price | setNumber}}</div>
          </div>
          <div class="totalCount">{{shareCarInfo.total_count | setPeople}}人正在免费提车</div>
        </div>
      </div>
      <div class="rect"></div>
      <!-- 顶部倒计时 -->
      <div class="countTime">
        <div class="top_title flex-row"><span>免费提车倒计时</span></div>
        <div class="flex-row timeBox jusB">
          <div class="time">{{countArr ? countArr[0] : '00'}}</div>
          <div class="timeTip">天</div>
          <div class="time">{{countArr ? countArr[1] : '00'}}</div>
          <div class="timeTip">时</div>
          <div class="time">{{countArr ? countArr[2] : '00'}}</div>
          <div class="timeTip">分</div>
          <div class="time">{{countArr ? countArr[3] : '00'}}</div>
          <div class="timeTip">秒</div>
        </div>
        <div class="line"></div>
      </div>
      <!-- 背景 -->
      <div class="rect"></div>
      <!-- 区分环境不同分享 -->
      <div class="browserBox">
        <div class="invite">邀您和我一起免费换座驾</div>
        <div class="userInfo">
          <div class="avatar iconSize">
            <img v-if="userInfo.avatar" :src="userInfo.avatar" alt="头像">
            <img v-else src="../../../assets/img/my_log.png" alt="头像">
          </div>
          <div class="nickname">{{userInfo.nickname ? $utils.splitStr(userInfo.nickname, 14) : '掘金宝'}}</div>
        </div>
        <!-- 是微信 -->
        <div class="share isWx" v-if="$utils.isWx()">
          <div class="qrbox">
            <div class="title">长按识别下方二维码</div>
            <div class="title title_sec">和我一起免费领座驾</div>
            <div class="qrLine">
              <div class="qrcode iconSize">
                <img :src="`${$config.currentApi}/v1/wechat/qrcode?invitetion_code=${$route.query.inviteCode}`" alt="公众号二维码">
              </div>
            </div>
            
          </div>
        </div>
        <div class="share" v-else>
          <div class="button flex-row" @click="downLoadApp">立即参与</div>
        </div>
        <!-- 不是微信 -->
      </div>
      <div class="rect"></div>
      <!-- 底部大盘 -->
      <div class="bottomImage">
        <div class="title">看新闻赚豪车赚洋房</div>
        <div class="juejinbao flex-row" :class="{isAndroid: $utils.isAndroid()}">
          <div class="logo iconSize">
            <img src="../../../assets/img/my_log.png" alt="logo">
          </div>
          <div class="dot"></div>
          <div class="text">掘金宝</div>
        </div>
      </div>
      
    </div>
    <ExitConfirm v-if="showConfirm" :carInfo='shareCarInfo' @closeConfirm='closeConfirm'></ExitConfirm>
    <carShare v-if="showCarShare && $route.path === '/share_page/1'" @shareSuccess='shareSuccess' :appShare='appShare' :fatherParams='fatherParams'></carShare>
  </div>
</template>
<script>
import headerNav from '@/components/task/header-nav'
import ExitConfirm from '@/components/share/share_page1/exit_tip'
import carShare from '@/components/share/carShare'
import LoopMessage from './loop_message'
import {mapState, mapMutations} from 'vuex'
export default {
  components: {
    headerNav,  // 头部导航
    ExitConfirm, // 退出确认弹框
    carShare, // 汽车分享
    LoopMessage // 顶部导航数据
  },
  computed: mapState({
    commonState: state => state.common
  }),
  data () {
    return {
      shareCarInfo: {}, // 汽车信息
      deadTime: '2018/12/15 14:00:00', // 倒计时截止时间
      countArr: null,
      qrcode: '', // 二维码链接
      ramdomList: [], // 随机列表
      hasReward: true,
      showConfirm: false, // 显示拦截框
      showCarShare: false, // 是否显示分享框
      appShare: {
        count: 0
      },
      loopList: [], // 汽车数据
      userInfo: {
        nickname: '掘金宝',
        avatar: require('../../../assets/img/my_log.png')
      }, // 邀请人信息
      toUrl: ''
    }
  },
  beforeRouteLeave (to, from, next) {
    if (this.hasReward) {
      this.showConfirm = true
      next(false)
    } else {
      this.showConfirm = false
      next()
    }
  },
  filters: {
    setNumber (value) {
      return value > 10000 ? ((value / 10000).toFixed(2) + '万') : value
    }
  },
  methods: {
    ...mapMutations([
      
    ]),
    // 跳转至app
    toApp () {
      this.$toast({
        message: '领取成功，请在app打开',
        duration: 1500
      })
      if (this.$config.source_style < 6) {
        if (this.$utils.isWx()) {
          if (this.$utils.isIos()) {
            window.open(this.$config.iOSUrl)
          } else if (this.$utils.isAndroid()) {
            window.open(this.$config.applicationUrl)
          }
        }
      } else {
        this.$utils.openApp({type: 'goods', path: this.$route.path})
      }
    },
    // 返回
    back () {
      this.$router.go(-1)
    },
    // 关闭弹框时做的操作
    closeConfirm ({action}) {
      if (action === 'confirm') {
        this.showConfirm = false
      } else if (action === 'cancel') {
        this.showConfirm = false
        this.hasReward = false
        this.$router.replace('/')
      }
    },
    // 分享成功
    shareSuccess () {

    },
    // 获取邀请人信息
    getInviteInfo () {
      if (this.$route.query.inviteCode) {
        this.$api.get('USER_INVITEINFO', {invitation_code: this.$route.query.inviteCode}, res => {
          if (res.data) {
            this.userInfo = res.data
          }
        })
      }
    },
    // 获取车辆信息
    getCarInfo () {
      let data = {}
      if (this.$route.query.gid) {
        data.gid = this.$route.query.gid
        // 存在商品id时重新获取文案
        this.getFatherParams()
      }
      this.$api.get('SHARE_GUILDBROWERS', data, res => {
        this.shareCarInfo = res.data
        if (!res.data.start_time) {
          this.countArr = [180, 0, 0, 0]
        } else {
          this.initTime(new Date(res.data.start_time.replace('-', '/') + ' 00:00:00').getTime() / 1000 + 180 * 24 * 3600)
        }
        // 不存在id时当做分享文案
        if (!this.$route.query.gid) {
          this.fatherParams = {
            title: `180天就能免费赚豪车赚洋房？！${res.data.total_count}人为此彻夜未眠！`,
            desc: `过年将${res.data.brand_name}提回家，赶紧上车 >`,
            img_url: res.data.thumb,
            gid: res.data.id
          }
          this.appShare.path = this.$route.path
          this.showCarShare = true
        }
      })
    },
    // 获取随机文案
    getFatherParams () {
      this.$api.get('SHARE_GUILDBROWERS', {}, res => {
        this.fatherParams = {
          title: `180天就能免费赚豪车赚洋房？！${res.data.total_count}人为此彻夜未眠！`,
          desc: `过年将${res.data.brand_name}提回家，赶紧上车 >`,
          img_url: res.data.thumb,
          gid: res.data.id
        }
        this.appShare.path = this.$route.path
        this.showCarShare = true
      })
    },
    // 获取倒计时秒数
    getRemain (deadTime) {
      this.remainTime = deadTime - this.$utils.getTimeStamp()
      this.countArr = this.$utils.getCountText(deadTime)
      if (this.remainTime <= 0) {
        clearTimeout(this.timer)
        this.timer = 0
      }
    },
    // 获取车轮播图
    getCarLoop () {
      this.$api.get('SHAER_BROADCAST', {}, res => {
        // this.carImage = res.data
        // this.initSwiper()
      })
    },
    // 初始化定时器
    initTime (deadTime) {
      if (this.timer) {
        clearInterval(this.timer)
        this.timer = null
      }
      this.getRemain(deadTime)
      this.timer = setInterval(() => {
        this.getRemain(deadTime)
      }, 1000)
    },
    // 获取随机文档
    getRandom () {
      this.$api.get('SHARE_LIST', {}, res => {
        this.loopList = res.data
      })
    },
    // 下载app
    downLoadApp () {
      this.$utils.openDownUrl()
    },
    // 初始化轮播时间
    initData () {
      this.getCarInfo()
      this.getRandom()
      this.getInviteInfo()
    }
  },
  mounted () {
    this.initData()
  }
}
</script>
<style lang="scss" scoped>
#random{
  height: 100%;
  box-sizing: border-box;
  position: relative;
  overflow: auto;
  .carSwiper{
    height: 563px;
    overflow: hidden;
    position: relative;
    z-index: 0;
    .swiper-pagination {
      bottom: 70px;
    }
    .bgSize{
      background-size: 100%;
      height: 100%;
    }
    .loopList {
      bottom: 100px;
    }
    .open_app {
      position: absolute;
      z-index: 100;
      right: 0px;
      top: 20px;
      background: rgba($color: #000, $alpha: .5);
      color: #fff;
      height: 50px;
      border-radius: 25px 0 0 25px;
      padding: 0 20px;
      font-size: 24px;
      a{
        color: #fff;
        text-decoration: none;
      }
    }
  }
  .carInfo{
    color: #fff;
    background: #fff;
    background-size: 100% 100%;
  }
  // 顶部
  .top{
    margin: -50px auto 34px;
    font-size: 32px;
    background: #fff;
    background: url('../../../assets/img/share/share_page1_title.png') no-repeat center;
    background-size: 100% 100%;
    position: relative;
    overflow: hidden;
    box-sizing: border-box;
    height: 288px;
    color: rgb(34, 81, 150);
    .title{
      color: #333;
      margin: 9px auto 0;
      line-height: 50px;
      font-size: 36px;
      font-weight: 600;
    }
    .brand_logo{
      width: 130px;
      height: 130px;
      margin: 20px auto 0;
    }
    .topBody{
      width: 642px;
      margin: 0 auto;
      padding: 0 26px;
      margin-top: -10px;
      height: 57px;
      white-space: nowrap;
      box-sizing: border-box;
      display: flex;
      align-items: flex-end;
      line-height: 1;
      color: #2A2A2A;
      .leftPrice {
        display: flex;
        align-items: flex-end;
      }
      .price {
        color: #2A2A2A;
        font-weight: 600;
        display: flex;
        align-items: flex-end;
        .redColor {
          color: #d96e0a;
          font-size: 60px;
          margin-bottom: -6px;
          display: inline-block;
        }
      }
      .acturally {
        text-decoration: line-through;
        font-size: 30px;
        color: #d8d8d8;
        margin: 0 10px;
      }
      .totalCount {
        font-size: 26px;
        color: #F5A623;
        font-weight: 900;
      }

    }
  }
  .countTime{
    .top_title{
      height: 70px;
      font-size: 40px;
      color: #333;
      line-height: 60px;
      width: 584px;
      background: url('../../../assets/img/share/share_page1_count_title.png') no-repeat center;
      background-size: 100%;
      font-size: 36px;
      margin: 30px auto;
      font-weight: 600;
    }
    .line {
      background: url('../../../assets/img/share/line.png') no-repeat center;
      width: 632px;
      height: 4px;
      margin: 35px auto 26px;
      background-size: 100% 100%;
    }
    .timeBox {
      width: 600px;
      margin: 0 auto 0;
      height: 82px;
      align-items: flex-end;
      .time {
        width: 120px;
        height: 84px;
        box-sizing: border-box;
        border-radius: 10px;
        padding-top: 6px;
        display: flex;
        flex-flow: row nowrap;
        align-items: center;
        line-height: 90px;
        justify-content: center;
        font-size: 60px;
        font-weight: 600;
        text-align: center;
        background: #F5A623;
      }
      .timeTip{
        color: #333;
        margin: 0 20px;
        font-size: 26px;
        line-height: 34px;
        font-weight: 900;
        &:last-child{
          margin-right: 0;
        }
      }
    }
  }
  // 白线
  .rect{
    height: 26px;
    background: #eee;
  }
  .browserBox {
    overflow: hidden;
    .invite {
      background: url('../../../assets/img/share/share_page1_invitetitle.png') no-repeat left top;
      height: 106px;
      background-size: 100%;
      padding-top: 12px;
      text-align: center;
      font-size: 36px;
      color: #333333;
      font-weight: 900;
      line-height: 50px;
      margin-top: 30px;
    }
    .userInfo {
      margin-top: -10px;
      color: #333;
      line-height: 45px;
      display: flex;
      flex-flow: column wrap;
      justify-content: center;
      align-items: center;
      .avatar {
        width: 80px;
        height: 80px;
        margin-right: 20px;
        overflow: hidden;
        border-radius: 50%;
      }
      .nickname {
        line-height: 45px;
        font-size: 32px;
        margin-top: 10px;
      }
    }
    .qrbox {
      background: url('../../../assets/img/share/share_qrcode.png') no-repeat center;
      margin: 20px auto 29px;
      width: 555px;
      height: 648px;
      background-size: 100%;
      position: relative;
      .title {
        position: absolute;
        top: 86px;
        line-height: 42px;
        font-size: 30px;
        left: 157px;
      }
      .title_sec {
        top: 148px;
      }
      .qrLine {
        position: absolute;
        left: 54%;
        bottom: 84px;
        transform: translateX(-50%);
        overflow: hidden;
        // border: 4px solid #F5A623;
        border-radius: 5px;
        .qrcode {
          width: 288px;
          height: 288px;
          margin: 20px;
        }
      }
      
    }
    
    .button {
      margin: 20px auto 26px;
      color: #fff;
      font-weight: 600;
      font-size: 32px;
      width: 570px;
      height: 73px;
      border-radius: 8px;
      background: #F5A623;
    }
  }
  .bottomImage{
    position: relative;
    background: url('../../../assets/img/task/send_car.png');
    background-size: 100% 100%;
    overflow: hidden;
    height: 351px;
    font-size: 20px;
    line-height: 2;
    text-align: left;
    color: #333;
    font-weight: 600;
    .title {
      margin: 34px 34px 11px;
      line-height: 56px;
      font-size: 40px;
    }
    .title_sec {
      margin-left: 34px;
      line-height: 56px;
      font-size: 36px;
    }
  }
  .juejinbao {
    position: absolute;
    font-size: 28px;
    font-weight: 900;
    color: #FF7200;
    bottom: 22px;
    right: 28px;
    display: flex;
    align-items: center;
    align-content: center;
    &.juejinbao {
      .text {
        padding-top: 4px;
        height: 44px;
        line-height: 48px;
      }
    }
    
    .logo{
      width: 45px;
      height: 45px;
      margin-right: 5px;
    }
  }
}
</style>



